.h5p-add-dialog-table > .h5p-dialog-box{
    width: 100% !important;
    box-sizing: border-box;
    margin: 0 !important;
    padding: 2rem !important;
}

.kaltura-integration{
    border: 1px solid #ababab;

    h3{
        text-align: left !important;

        &:first-child{
            margin-top: 0 !important;
        }
    }
    select{
        width: 100%;
    }
    .kaltura-integration-accordion{
        display: grid;
        grid-template-columns: 1fr 15px;
        grid-gap: 20px;
        align-items: center;
        cursor: pointer;
        padding: 20px;
    }
    .h5peditor-field-description{
        margin-bottom: 0;
    }

    .h5p-notice{
        margin: 15px 0;
        padding-right: 38px;
        position: relative;
        background: #fff;
        border: 1px solid #c3c4c7;
        border-left-width: 4px;
        padding: 1px 12px;
    
        &.valid{
            border-left-color: #00a32a;
        }
    
        &.invalid{
            border-left-color: #B32D2E;
        }
    
        p{
            margin: 0.5em 0 !important;
            padding: 2px;
            padding-right: 20px;
        }
      }
    
    .h5p-divider{
        border-bottom: 1px solid #ababab;
        margin-top: 1rem;
        margin-bottom: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;
    
        &::after{
            content: 'or';
            transform: translateY(50%);
            background: #fff;
            padding: 0 15px;
        }
    }
}

.form-manager-slidein{
    overflow-y: scroll;
}

